body,
html {
  height: 100%;
}

.all,
.showcode {
  position: relative;
}

.acc,
.input {
  cursor: auto;
}

.accHeaderText,
.footer,
body {
  font-family: Ubuntu,sans-serif;
}

html {
  box-sizing: border-box;
  overflow-x: hidden;
}

*,
:after,
:before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: #fff;
  font-size: 1em;
  color: #000;
}

.accordion-content a,
.center-text a,
.description a,
.dropdown-content a,
.dropnav a,
.navbar a,
.sidenav a,
.topnav a,
.twitch-item a,
li a {
  text-decoration: none!important;
  color: #000;
}

.accordion-content a,
.btn,
.btn-block,
.btn-floating,
.btn-floating-large,
.center-text a,
.closebtn,
.closenav,
.description a,
.dropdown-click:hover,
.dropdown-content a,
.dropdown-hover:hover,
.dropnav a,
.hover-grayscale,
.hover-greyscale,
.hover-opacity,
.hover-opacity-off,
.hover-sepia,
.hover-shadow,
.hoverable li,
.hoverable tbody tr,
.navbar a,
.opennav,
.pagination li a,
.sidenav a,
.twitch-item a {
  -webkit-transition: background-color .3s,color .15s,box-shadow .3s,opacity .3s,filter .3s;
  transition: background-color .3s,color .15s,box-shadow .3s,opacity .3s,filter .3s;
}

#twitch-list {
  display: -webkit-flex;
  display: flex;
  flex-flow: wrap;
}

.list-container-center {
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-align-content: center;
  -ms-flex-line-pack: center;
  align-content: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
}

.list-container {
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-align-content: flex-start;
  -ms-flex-line-pack: flex-start;
  align-content: flex-start;
  -webkit-align-items: flex-start;
  -ms-flex-align: flex-start;
  align-items: flex-start;
}

.list-item {
  margin: 1px;
  padding: .3em;
  color: #fff;
  background-color: #358ccb;
  flex: 0 0 32%;
  -webkit-align-self: stretch;
  -ms-flex-item-align: stretch;
  align-self: stretch;
  box-shadow: 0 8px 16px 0 rgba(0,0,0,.2),0 6px 20px 0 rgba(0,0,0,.19);
}

.center-text,
.input {
  color: #358ccb;
  padding: 1em;
}

.box2 {
  display: inline-block;
  width: 200px;
  height: 100px;
  margin: 1em;
}

.input {
  display: block;
  -webkit-appearance: textfield;
  background-color: #fff;
  -webkit-rtl-ordering: logical;
  user-select: text;
  border-width: 1px;
  border-style: solid;
  border-color: #358ccb;
  width: 18em;
}

.center-block {
  display: flex;
  flex-direction: row;
  flex: 1 30%;
  justify-content: center;
  flex-wrap: wrap;
}

.card-btn {
  display: flex;
  background-color: #358ccb;
}

.card-btn a {
  display: block;
  width: 100%;
  padding: 1em;
  color: #fff;
  text-decoration: none!important;
}

.small {
  font-size: .6em;
}

.right {
  float: right!important;
}

.left {
  float: left!important;
}

.all {
  min-height: 100%;
  margin-left: 300px;
  transition: margin-left .4s;
  background-color: #fdfdfd;
}

.challenge {
  font-size: 1.3em;
  padding: .7em;
  background-color: orange;
  height: 50px;
}

.description {
  padding: 1.6em;
  font-size: .85em;
  background-color: #358ccb;
  color: #fff;
}

.description-links {
  list-style: none;
}

.description-links li {
  margin: 12px;
}

.description-links a {
  color: orange;
  text-decoration: none!important;
}

.codework {
  padding: .7em .7em 55px;
  background-color: #fdfdfd;
}

.language-js {
  font-size: .9em;
  line-height: normal;
}

.runcode {
  margin-top: 5px;
  margin-bottom: 5px;
}

.acc,
ul {
  margin: 0;
}

.runcode a,
.runcode a:link {
  background-color: #358ccb;
  color: #fff;
  padding: 2px;
  font-size: .85em;
  text-align: left;
  display: block;
  text-decoration: none!important;
}

.card-btn a:hover,
.description a:hover,
.runcode a:hover {
  background-color: #2f9c0a;
  color: #fff;
  text-decoration: none!important;
}

.showcode {
  display: block;
}

.showcode .overlay {
  background: #2f9c0a;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  position: absolute;
  padding: 0;
  transition: opacity .5s;
  z-index: 97;
  display: none;
}

.showcode .overlay .result {
  position: relative;
  top: 40%;
  width: 100%;
  color: #fff;
  font-weight: 800;
  text-align: center;
  font-size: 2em;
  display: none;
}

.closeOverlay,
.closenav,
.footer {
  position: absolute;
}

.footer {
  font-size: .9em;
  padding: .7em;
  background-color: orange;
  height: 55px;
  bottom: 0;
  left: 0;
  width: 100%;
  text-align: center;
}

.sidenav-header {
  background-color: #2f9c0a;
  color: #fff;
  height: 50px;
}

.sidenav-header a,
.sidenav-header a:link,
.sidenav-header a:visited {
  color: #fff;
  font-size: 1.3em;
  padding-left: .7em;
  background-color: #2f9c0a;
}

.closenav {
  top: .7em;
  right: 1em;
}

.closeOverlay {
  color: #fff;
  top: 1em;
  right: 1em;
  font-size: 2em;
}

.sidenav {
  height: 100%;
  width: 300px;
  background-color: #fff;
  position: fixed!important;
  z-index: 99;
  overflow: auto;
}

.acc,
.animate-left,
ul {
  position: relative;
}

.sidenav a {
  font-size: .8em;
  color: #2f9c0a;
  padding: .7em .5em .2em 1em;
}

.sidenav a:hover {
  background-color: #ccc;
}

.sidenav-header a:hover {
  background-color: #2f9c0a;
}

.dropnav a,
.sidenav a {
  display: block;
}

.acc,
.hide {
  display: none;
}

.dropnav a:hover,
.sidenav .dropdown-click:hover,
.sidenav .dropdown-hover:first-child,
.sidenav .dropdown-hover:hover {
  background-color: #ccc;
}

.sidenav .dropdown-click,
.sidenav .dropdown-hover {
  width: 100%;
}

.sidenav .dropdown-click .dropdown-content,
.sidenav .dropdown-hover .dropdown-content {
  min-width: 100%;
}

.fa .fa-plus-square-o .fa-fw {
  width: 1.28571429em;
  text-align: center;
}

.acc {
  width: 100%;
  padding-left: 1.5em;
}

.show {
  display: block!important;
}

ul {
  display: block;
  padding: .5em;
  list-style: none;
}

display: block; position: relative; li ul {
  top: 1em;
  left: 1em;
}

li>ul {
  top: auto;
  left: auto;
}

.accHeader:hover {
  background-color: #358ccb;
  opacity: .7;
  color: #fff;
}

.accHeaderShow {
  color: #fff!important;
  background-color: #358ccb;
  display: block!important;
}

.accHeaderText {
  padding-left: .7em;
}

.accHeader {
  display: block!important;
  padding: .7em;
  cursor: pointer;
  color: #358ccb;
}

.animate-left {
  -webkit-animation: animateleft .4s;
  animation: animateleft .4s;
}

@-webkit-keyframes animateleft {
  from {
    left: -300px;
    opacity: 0;
  }

  to {
    left: 0;
    opacity: 1;
  }
}

@keyframes animateleft {
  from {
    left: -300px;
    opacity: 0;
  }

  to {
    left: 0;
    opacity: 1;
  }
}

.card {
  border: 1px solid #ccc;
}

.card4,
.hover-shadow:hover {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,.2),0 6px 20px 0 rgba(0,0,0,.19)!important;
}

.modal {
  z-index: 3;
  display: none;
  padding-top: 100px;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #000;
  background-color: rgba(0,0,0,.4);
}

.modal-content {
  margin: auto;
  background-color: #fff;
  position: relative;
  padding: 1em;
  outline: 0;
  width: 600px;
}

.closebtn {
  text-decoration: none;
  float: right;
  font-size: 3em;
  font-weight: 700;
  color: #fff;
  background-color: red;
  width: 1.3em;
  text-align: center;
  height: 1.3em;
}

.intermediate-btn {
  width: 300px;
}

.closebtn:focus,
.closebtn:hover {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

.display-topright {
  position: absolute;
  right: 0;
  top: 0;
}

@media (min-width:992px) {
  .hide-large {
    display: none!important;
  }

  .sidenav {
    display: block;
  }
}

@media (min-width:993px) {
  .modal-content {
    padding: 1em;
    width: 900px;
  }
}

@media screen and (max-width:992px) {
  .sidenav {
    display: none;
  }

  .all {
    margin-left: 0!important;
    margin-right: 0!important;
  }
}

@media (max-width:768px) {
  .modal-content {
    padding: 1em;
    width: 500px;
  }

  .modal {
    padding-top: 50px;
  }
}

@media (max-width:600px) {
  .modal-content {
    padding: 1em;
    margin: 0 10px;
    width: auto!important;
  }

  .modal {
    padding-top: 30px;
  }
}

@media screen and (max-width:540px) {
  .challenge {
    height: 120px;
  }
}